/**
 * Vuex
 *
 * @library
 *
 * https://vuex.vuejs.org/en/
 */

// Lib imports
import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

// Store functionality
// import actions from './actions'
// import getters from './getters'
import modules from './modules'
// import mutations from './mutations'
// import state from './state'

Vue.use(Vuex)

// Create a new store
const store = new Vuex.Store({
  actions: {
    register({commit}, user) {
      return new Promise((resolve, reject) => {
        commit('auth_request');
        axios({url: 'http://localhost:8080/registerInstitution', data: user, method: 'POST'})
            .then(resp => {
              const token = resp.data.token;
              localStorage.setItem('token', token);
              axios.defaults.headers.common['Authorization'] = token;
              commit('auth_success', token);
              resolve(resp);
            })
            .catch(err => {
              commit('auth_error', err);
              localStorage.removeItem('token');
              reject(err);
            })
      })
    },
    login ({commit}, user) {
      return new Promise((resolve, reject) => {
        commit('auth_request');
        axios({url: 'http://localhost:8080/authenticateInstitution', data: user, method: 'POST'})
            .then(resp => {
              const token = resp.data.token;
              localStorage.setItem('token', token);
              axios.defaults.headers.common['Authorization'] = token;
              commit('auth_success', token);
              resolve(resp)
            })
            .catch(err => {
              commit('auth_error');
              localStorage.removeItem('token');
              reject(err)
            })

      })
    },
    logout({commit}) {
      return new Promise((resolve) => {
        commit('logout');
        localStorage.removeItem('token');
        delete axios.defaults.headers.common['Authorization'];
        resolve()
      })
    },
    gotoAndCarry({commit}, obj){
  console.log("gotoAndCarry");
    return new Promise((resolve) => {
  commit('goto_and_carry', obj);

  resolve();
})
},
gotoAndCarryNU({commit}, obj){
console.log("gotoAndCarryNU");
return new Promise((resolve) => {
commit('goto_and_carry_NU', obj);

resolve();
})
},
wasCarried({commit}){
    return new Promise((resolve) => {
  commit('was_carried');
  resolve();
})
}, wasCarriedNU({commit}){
    return new Promise((resolve) => {
  commit('was_carried_NU');
  resolve();
})
}

  },
  getters: {
    isLoggedIn: state => !!state.token,
    authStatus: state => state.status
  },
  modules,
  mutations: {
    auth_request(state) {
      state.status = 'loading'
    },
    auth_success(state, token) {
      state.status = 'success';
      state.token = token;
    },
    auth_error(state) {
      state.status = 'error'
    },
    logout(state) {
      state.status = '';
      state.token = '';
    },
    goto_and_carry(state, obj){
      console.log("Carried obj: " + obj.name);
      state.carriedObject = obj;
      state.redirected = true;
      console.log("redirected: " + state.redirected);
    },
    was_carried(state){
      state.redirected = false;
    },
    goto_and_carry_NU(state, obj){
      console.log("Carried obj NU: " + obj.name);
      state.carriedObjectNU = obj;
      state.redirectedNU = true;
      console.log("redirected: " + state.redirected);
    },
    was_carried_NU(state){
      state.redirectedNU = false;
    }
  },
  state: {
    redirected: false,
    carriedObject : {},
    redirectedNU: false,
    carriedObjectNU : {},
    status: '',
    token: localStorage.getItem('token') || ''
  }
});

export default store
