<template>
  <v-app-bar
    dark
    class="ma-0 pa-0"
    flat
    height="70"
  >
    <router-link to="/">
      <img
        src="@/assets/logo_fill_4.png"
        height="100"
      >
    </router-link>
    <v-spacer />
    <v-btn
      color="blue lighten-2"
      dark
      class="ma-5"
      href="/institutionloggedin"
    >
      Edit Info
    </v-btn>

    <v-btn
      color="blue lighten-2"
      dark
      class="ma-5"
      href="/institutionProfile"
    >
      Profile
    </v-btn>

    <v-btn
      color="blue lighten-2"
      dark
      class="ma-5"
      href="/addProgram"
    >
      Programs
    </v-btn>

    <v-btn
      color="blue lighten-2"
      dark
      class="ma-5"
      href="/addCourse"
    >
      Courses
    </v-btn>

    <v-btn
      color="blue lighten-2"
      dark
      class="ma-5"
      href="/institution"
      @click="logout"
    >
      Logout
    </v-btn>
  </v-app-bar>
</template>


<script>

import {
  mapMutations
} from 'vuex'

export default {
  data: () => ({

  }),

  watch: {
    '$route' (val) {
      this.title = val.name
    }
  },

  mounted () {
    this.onResponsiveInverted()
    window.addEventListener('resize', this.onResponsiveInverted)
  },
  beforeDestroy () {
    window.removeEventListener('resize', this.onResponsiveInverted)
  },

  methods: {
    ...mapMutations('app', ['setDrawer', 'toggleDrawer']),
    onClickBtn () {
      this.setDrawer(!this.$store.state.app.drawer)
    },
    logout () {
      this.$store.dispatch('logout')
              .then(() => {
                this.$router.push('/institution')
              })
    },
    onResponsiveInverted () {
      if (window.innerWidth < 991) {
        this.responsive = true
      } else {
        this.responsive = false
      }
    }
  }
}
</script>

<style>
  #core-toolbarinstitution a {
    text-decoration: none;
  }
</style>
