<template>
  <div>
    <v-navigation-drawer
      v-model="drawer"
      permanent
      expand-on-hover
      absolute
    >
      <v-list-item class="px-2">
        <v-img src="https://www.ucalgary.ca/brand/system/files/uc-vert-rgb-sml.jpg" />


        <v-list-item-title>University of Calgary</v-list-item-title>

        <v-btn
          icon
          @click.stop="mini = !mini"
        >
          <v-icon>mdi-chevron-left</v-icon>
        </v-btn>
      </v-list-item>

      <v-divider />

      <v-list dense>
        <v-list-item
          v-for="link in links"
          :key="link.text"
          link
        >
          <v-list-item-icon>
            <v-icon>{{ link.icon }}</v-icon>
          </v-list-item-icon>

          <v-list-item-content>
            <v-list-item-title>{{ link.text }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </div>
</template>


<script>
// Utilities
import {
  mapMutations,
  mapState
} from 'vuex'

export default {
  props: {
    opened: {
      type: Boolean,
      default: false
    }
  },
  data: () => ({
    logo: 'https://www.sccpre.cat/mypng/detail/332-3325815_admin-comments-transparent-background-admin-icon.png',
    links: [

      {
        to: '/university-profile',
        icon: 'mdi-account',
        text: 'University Profile'
      },
      {
        to: '/university-courses',
        icon: 'mdi-account',
        text: 'University Courses'
      },
      {
        to: '/',
        icon: 'mdi-cancel',
        text: 'Logout'
      }

    ]
  }),
  computed: {
    ...mapState('app', ['image', 'color']),
    inputValue: {
      get () {
        return this.$store.state.app.drawer
      },
      set (val) {
        this.setDrawer(val)
      }
    },
    items () {
      return this.$t('Layout.View.items')
    }
  },

  methods: {
    ...mapMutations('app', ['setDrawer', 'toggleDrawer'])
  }
}
</script>

<style lang="scss">
  #app-drawer {
    .v-list__tile {
      border-radius: 4px;

      &--buy {
        margin-top: auto;
        margin-bottom: 17px;
      }
    }
  }
</style>
